<template>
  <div class="active-dynamic-details-container">
    <div class="background-box"></div>
    <div class="container">
      <div class="header flex-start">
        <div class="header-left">{{ dateFormat }}</div>
        <div class="header-right">
          <van-tag type="primary" size="large "
            >{{ activityInfo.city }}{{ activityInfo.district
            }}{{ activityInfo.street }}</van-tag
          >
          <p class="header-date-style">
            {{ ymFormat }}
            {{ weekFormat }}
            {{ activityInfo.duration / 3600 }}小时
          </p>
        </div>
        <van-button
          class="delete-btn-style"
          size="small"
          round
          @click="onDelete"
        >
          <van-image
            class="delete-icon-style"
            :src="require('@/assets/icon/delete_icon.png')"
          />
          删除
        </van-button>
      </div>
      <Card class="basic-infor-box">
        <div class="basic-info-headre">
          <p class="basic-info-title">{{ activityInfo.title }}</p>
          <div class="tag-box-style">
            <van-tag
              class="tag-style"
              v-for="(item, index) in activityInfo.tag_info"
              :key="index"
              type="primary"
              size="medium"
              >{{ item.name }}</van-tag
            >
          </div>
        </div>
        <div class="basic-info-main">
          <div class="basic-info-main-row flex-start">
            <p class="basic-info-main-row-label">举办单位:</p>
            <p class="basic-info-main-row-value">
              {{ activityInfo.host_info ? activityInfo.host_info.name : '' }}
            </p>
          </div>
        </div>
      </Card>

      <!-- 现场记录 -->
      <Record
        :activityId="$route.query.id"
        :activityVisitor="activityInfo.visitor"
      />

      <!-- 举办单位反馈表&主讲人承诺书 -->
      <Certificate
        :activityId="$route.query.id"
        :file1="activityInfo.file1"
        :file2="activityInfo.file2"
      />

      <!-- 活动海报-延后 -->
      <!-- <ActivityPosters /> -->

      <!-- 听众评价 -->
      <!-- <AudienceEvaluation class="audience-evaluation-style" /> -->
    </div>

    <!-- 二维码-延后 -->
    <!-- <van-button class="footer-btn-style" type="info" size="large" block
      >签到二维码</van-button
    > -->
  </div>
</template>

<script>
export default {
  components: {
    Card: () => import('@/components/Card'),
    Avatar: () => import('@/components/Avatar'),
    Logo: () => import('@/components/Logo'),
    NoRecord: () => import('@/components/NoRecord'),
    Record: () => import('./components/Record'),
    Certificate: () => import('./components/Certificate'),
    ActivityPosters: () => import('./components/ActivityPosters'),
    AudienceEvaluation: () => import('./components/AudienceEvaluation'),
  },
  computed: {
    dateFormat() {
      if (this.activityInfo.start_date) {
        return this.$dateFormat(this.activityInfo.start_date, 'dd')
      } else {
        return ''
      }
    },
    ymFormat() {
      if (this.activityInfo.start_date) {
        return this.$dateFormat(this.activityInfo.start_date, 'yyyy/MM')
      } else {
        return ''
      }
    },
    weekFormat() {
      if (this.activityInfo.start_date) {
        return this.$dateFormat(this.activityInfo.start_date, 'ddd')
      } else {
        return ''
      }
    },
  },
  data() {
    return {
      // TODO: 测试是否显示数据
      activityInfo: {},
      show: false,
    }
  },
  created() {
    this.getActivityInfo()
  },
  methods: {
    getActivityInfo() {
      let id = this.$route.query.id
      this.$request('lecture.eventDetails', { id: id }).then((info) => {
        this.activityInfo = info
      })
    },
    onDelete() {
      console.log('delete :>>')
      this.$dialog
        .confirm({
          title: '',
          message: '您将删除该活动信息，是否继续？',
          confirmButtonColor: '#2374FF',
          cancelButtonColor: '#999999',
        })
        .then(() => {
          this.$request('lecture.lectureDelInstructor', {
            id: this.activityInfo.id,
            is_del: 1,
          }).then((res) => {
            this.$router.back()
          })
        })
        .catch(() => {
          console.log('取消删除 :>>')
        })
    },
  },
}
</script>

<style lang="scss" scoped>
.active-dynamic-details-container {
  position: relative;
  .background-box {
    height: px(340);
    background: linear-gradient(180deg, #0077ff 0%, #f7f7f7 100%);
    opacity: 0.6;
  }
  .container {
    padding: px(40) px(24);
    height: 340px;
    border-radius: 0px 0px 0px 0px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    .header {
      .header-left {
        font-size: px(120);
        font-weight: bold;
        color: $primary-color;
        opacity: 1;
      }
      .header-right {
        position: relative;
        margin-left: px(12);
        .header-date-style {
          margin-top: px(7);
          font-size: px(26);
          font-weight: bold;
        }
      }
      .delete-btn-style {
        position: absolute;
        top: px(96);
        right: px(24);
        .delete-icon-style {
          width: px(22);
          height: px(22);
        }
      }
    }
    .basic-infor-box {
      padding: px(32) px(24);
      .basic-info-headre {
        padding-bottom: px(32);
        border-bottom: px(1) solid #eeeeee;
        .basic-info-title {
          font-size: px(40);
          font-weight: bold;
        }
        .tag-box-style {
          margin-top: px(24);
          .tag-style {
            margin-right: px(9);
          }
        }
      }
      .basic-info-main {
        .basic-info-main-row {
          padding-top: px(24);
          .basic-info-main-row-label {
            font-size: px(30);
          }
          .basic-info-main-row-value {
            margin-left: px(10);
            font-size: px(30);
            font-weight: bold;
          }
        }
      }
    }
    .audience-evaluation-style {
      padding-bottom: px(249);
    }
  }

  .footer-btn-style {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
  }
}
</style>
